@use "base/colors";
@use "base/sizing";

// Text formatting

body {
  line-height: 1.3;
  color: colors.$off-black;
  font: {
    family: "Lato", "Helvetica", "Arial", sans-serif;
    size: 1.15rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
.subhead {
  font-weight: 400;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

.subhead {
  font-size: 1.3rem;
}

.longform {
  line-height: 1.5;
  font-size: 18px;

  @media (min-width: sizing.$screen-sm-min) {
    font-size: 21px;
  }

  p {
    margin: 0 0 1.2rem;
  }

  ol,
  ul {
    padding-left: 20px;
  }
}

a {
  text-decoration: none;
  color: colors.$link;

  &:hover {
    text-decoration: underline;
    color: colors.$link-hover;
  }

  &:active,
  &.active-link {
    color: colors.$link-active;
  }
}

pre,
code,
kbd,
samp {
  font-size: 1rem;
}

figure {
  @media (max-width: sizing.$screen-sm-min - 1) {
    margin-right: 0;
    margin-left: 0;
  }

  .longform & {
    video,
    img {
      width: 100%;
      object-fit: scale-down;
    }
  }
}

// Inspired by GitHub's rules
p code {
  border-radius: 3px;
  background-color: colors.$light-gray;
  padding: 0.2rem 0.4rem;
  font-size: 0.915rem;
}

// pre with syntax highlighting
pre.highlight {
  border-top: 1px solid colors.$light-gray;
  border-bottom: 1px solid colors.$light-gray;
  padding: 0.5rem 0;
  overflow-x: auto;
}
